export type validtypes = string | undefined

export const required = (value: string): validtypes => {
    if (value) return undefined;
    return 'Ошибка: Поле пустое';
}

export const maxLengthCreator = (maxLength: number) => (value: string): validtypes => {
    if (value && value.length > maxLength) return `Максимальное число символов: ${maxLength} `;
    return undefined;
}