import React, { createRef } from 'react';
import { Field, reduxForm } from 'redux-form';
import { maxLengthCreator, required } from '../../Utils/validator/validator';
import { Textarea, TextareaPostsAndMessages } from '../coummon/Prilouder/formControls/formControls';
import classes from './MyPosts.module.css';
import Posts from './Posts';
import ava from '../../assets/avaImg/mal2.jpg';
import Prilouder from '../coummon/Prilouder/Prilouder';



const MyPosts = React.memo((props) => {

    let posts = props.posts;
    let addPostC = props.addPostAC;

    let addPostBody = (value) => {
        addPostC(value.postBody)
    }


    let postsData = [...posts].reverse().map((p, index) => <Posts key={index} message={p.message} likes={p.likes} id={p.id} deletePost={props.deletePost} {...props} />)


    return (
        <div>
            {!props.profile ?
                <div style={{ marginLeft: '216px', width: "778px", height: "400px", boxShadow: "0 0 2.5px -1px black", borderRadius: "10px", padding: "50px 0 0 0", backgroundColor: "white" }}>
                    <h2 style={{ opacity: 0.2 }}>загрузка постов</h2>
                    <Prilouder />
                </div> :
                <div className={classes.MyPosts}>
                    <div className={classes.MyPostsForm}>
                        <AddPostReduxForm onSubmit={addPostBody} {...props} />
                    </div>
                    <div className={classes.publications}>
                        {/* <div>
                        {props.isOuner ?
                            <h2>
                                публикации от <div className={classes.moiPosts}>{props.profile.fullName}</div>
                            </h2> :
                            <h2>
                                публикации от <div className={classes.userPosts}>{props.profile.fullName}</div>
                            </h2>
                        }
                    </div> */}
                        <div className={classes.Post}>
                            {postsData}
                        </div>
                    </div>
                </div>}
        </div>
    )
});

const maxLength700 = maxLengthCreator(700);

const addPostForm = (props) => {

    if (!props.profile) {
        return null
    }

    return (
        <>
            {props.isOuner ?
                <form onSubmit={props.handleSubmit}>
                    <div className={classes.textareaButtonBlock}>
                        <img src={props.profile.photos.large ? props.profile.photos.large : ava} alt="user" />
                        <div className={classes.textareaButton}>
                            <Field component={TextareaPostsAndMessages} cols="65" placeholder="Напишите свой пост..." name="postBody" validate={[required]} />
                            <button className={classes.buttonForm}>Опубликовать</button>
                        </div>
                    </div>
                </form> :
                null}
        </>

    )
};

const AddPostReduxForm = reduxForm({ form: "messenger" })(addPostForm);



export default MyPosts;