import { Dispatch } from "redux";
import { ThunkAction } from "redux-thunk";
import { UsersAPI } from "../api/api";
import { photoType, usersType } from "../types/types";
import { upDataObjectArray } from "../Utils/objehelper";
import { AppStateType } from "./redux-store";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_PEOPLE';
const SET_CARRENT_PAGE = 'SET_CARRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOGGAL_FETCHING = 'TOGGAL_FETCHING';
const TOGGAL_IS_FOLLOWING_PROGRESS = 'TOGGAL_IS_FOLLOWING_PROGRESS';
const FOLLOW_OR_UNFOLLOW = "FOLLOW_OR_UNFOLLOW";


let initial = {
    users: [] as Array<usersType>,
    pageSise: 10 as number | null,
    totalCount: 0 as number | null,
    carrentPage: 1 as number | null,
    isFetching: true as boolean,
    toggalIsFollowing: [] as Array<number>,
    isfollowed: false as boolean
}

type initialType = typeof initial

let usersReducer = (state = initial, action: actionTypes): initialType => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: upDataObjectArray(state.users, action.usersId, 'id', { followed: true })
            }
        case UNFOLLOW:
            return {
                ...state,
                users: upDataObjectArray(state.users, action.usersId, 'id', { followed: false })
            }
        case SET_USERS:
            return {
                ...state,
                users: [...state.users = action.users]
            }

        case SET_CARRENT_PAGE:
            return {
                ...state,
                carrentPage: action.page
            }
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalCount: action.totalCount
            }

        case TOGGAL_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            }
        case TOGGAL_IS_FOLLOWING_PROGRESS:
            return {
                ...state,
                toggalIsFollowing: action.isFetching ? [...state.toggalIsFollowing, action.usersId]
                    : state.toggalIsFollowing.filter(id => id != action.usersId)
            }
        case FOLLOW_OR_UNFOLLOW: 
        return {
            ...state,
            isfollowed: action.isfollowed
        }

        default:
            return state;
    }
};

type actionTypes = followSuccessType | unfollowSuccessType | setUsersType | setCurrentPageType | setTotalusersCountType | setToggalType | toggalIsFollowingProgressType | getfollowedType

type getfollowedType = {type: typeof FOLLOW_OR_UNFOLLOW, isfollowed: boolean}
export let getfollowed = (isfollowed: boolean): getfollowedType => { return { type: FOLLOW_OR_UNFOLLOW, isfollowed } };

type followSuccessType = {type: typeof FOLLOW, usersId: number}
export let followSuccess = (usersId: number): followSuccessType => { return { type: FOLLOW, usersId } };

type unfollowSuccessType = {type: typeof UNFOLLOW, usersId: number}
export let unfollowSuccess = (usersId: number): unfollowSuccessType => { return { type: UNFOLLOW, usersId } };

type setUsersType = {type: typeof SET_USERS, users: Array<usersType>}
export let setUsers = (users: Array<usersType>): setUsersType => { return { type: SET_USERS, users } };

type setCurrentPageType = {type: typeof SET_CARRENT_PAGE, page: number}
export let setCurrentPage = (page: number): setCurrentPageType => { return { type: SET_CARRENT_PAGE, page } };

type setTotalusersCountType = {type: typeof SET_TOTAL_USERS_COUNT, totalCount: number}
export let setTotalusersCount = (totalCount: number): setTotalusersCountType => { return { type: SET_TOTAL_USERS_COUNT, totalCount } };

type setToggalType = {type: typeof TOGGAL_FETCHING, isFetching: boolean}
export let setToggal = (isFetching: boolean): setToggalType => { return { type: TOGGAL_FETCHING, isFetching } };

type toggalIsFollowingProgressType = {type: typeof TOGGAL_IS_FOLLOWING_PROGRESS, isFetching: boolean, usersId: number}
export let toggalIsFollowingProgress = (isFetching: boolean, usersId: number):toggalIsFollowingProgressType => { return { type: TOGGAL_IS_FOLLOWING_PROGRESS, isFetching, usersId } };



export let getUsersThunkCreater = (carrentPage: number, pageSise: number) => {
    return async (dispatch: Dispatch<actionTypes>, getState: AppStateType) => {
        dispatch(setToggal(true))
        let response = await UsersAPI.getUsers(carrentPage, pageSise)
            dispatch(setCurrentPage(carrentPage))
            dispatch(setUsers(response.data.items))
            dispatch(setTotalusersCount(response.data.totalCount))
            dispatch(setToggal(false))
    }
};

let followUnfollow = async (dispatch: Dispatch<actionTypes>, usersId: number, apiMethod: any, actionCreater: (usersId: number) => followSuccessType | unfollowSuccessType, isfollowed: boolean) => {
    dispatch(toggalIsFollowingProgress(true, usersId))
    let response = await apiMethod(usersId)
    if (response.data.resultCode == 0) {
        dispatch(actionCreater(usersId))
    }
    dispatch(toggalIsFollowingProgress(false, usersId))
    dispatch(getfollowed(isfollowed))
}

export let UnFollowThunkCreater = (usersId: number): ThunkAction<Promise<void>, AppStateType, unknown, actionTypes> => {
    return async (dispatch) => {
        followUnfollow(dispatch, usersId, UsersAPI.UnFollow.bind(UsersAPI), unfollowSuccess, false)
    }
}

export let FollowThunkCreater = (usersId: number) => {
    return async (dispatch: any) => {
        followUnfollow(dispatch, usersId, UsersAPI.Follow.bind(UsersAPI), followSuccess, true)
    }
}
export default usersReducer;