import axios from "axios";
import { profileType } from "../types/types";

const instance = axios.create({
    withCredentials: true,
    headers: {
        'API-KEY': 'ef8a4b9c-1df0-4c77-aba0-118c4d1a37cf'
    },
    baseURL: 'https://social-network.samuraijs.com/api/1.0/'
})

export const UsersAPI = {
    getUsers (carrentPage = 0, pageSise = 0) {
        return instance.get(`users?page=${carrentPage}&count=${pageSise}`)
        },
    UnFollow (id: number) {
        return instance.delete(`follow/${id}`)
    },
    Follow (id: number) {
        return instance.post(`follow/${id}`)
    }
};

export const ProfileAPI = {
    getStatus(peopleID: number) {
        return instance.get(`profile/status/${peopleID}`)
    },
    getProfile(peopleID: number) {
        return instance.get(`profile/${peopleID}`)
    },
    updateStatus(status: string) {
        return instance.put(`profile/status/`, {status: status})
    },
    savePhoto(photo: any) {
        const formData = new FormData();
        formData.append("image", photo)
        return instance.put(`profile/photo/`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
    },
    saveProfile(profile: profileType) {
        return instance.put(`profile`, profile)
    }
};

export enum resultCodes {
    success = 0,
    error = 1,
    captchaisrequired
}

type authType = {
    data: {
        id: number
        email: string
        login: string
    }
    resultCode: number
    messages: Array<string>
}

type loginType = {
    data: {
        id: number
    }
    resultCode: number
    messages: Array<string>
}

type logoutType = {
    data: any
    resultCode: number
    messages: Array<string>
}

export const AuthAPI = {
    Auth() {
        return instance.get<authType>(`auth/me`).then(res => res.data)
    },
    Login (email: string, password: string, rememberMe: boolean, captcha: string | null = null) {
        return instance.post<loginType>(`auth/login`, {email, password, rememberMe, captcha})
    },
    Logout () {
        return instance.delete<logoutType>(`auth/login`)
    }
}

export const SecurityApi = {
    getCatpchaUrl() {
        return instance.get(`security/get-captcha-url`);
    }
}