import React, { useState } from 'react';
import classes from './ProfileInfo.module.css'
import ProfileData from './profileData';
import WithReduxForm from './profileDataForm';

import ava from '../../../assets/avaImg/mal2.jpg';

const ProfileInfo = (props) => {

    let [editForm, SetEditForm] = useState(false);

    const onSubmit = (formData) => {
        props.saveInfo(formData)
    }

    const editFormInfo = () => {
        SetEditForm(!editForm)
    }

    let profile = props.profile;

    if (!props.profile) {
        return null
    }

    const mainPhoto = (e) => {
        if (e.target.files.length) {
            props.saveFile(e.target.files[0]);
        }
    }

    let u;


    return (
        <div className={classes.profileInfo}>
            <div className={classes.AvaImg}>
                <img src={profile.photos.large ? profile.photos.large : ava} alt="ava" />
                <div>
                    {props.isOuner ?
                        <div className={classes.addPhoto}>
                            <input name="file" type="file" id="file" onChange={(e) => mainPhoto(e)} />
                            <label for='file'>Добавить фото</label>
                        </div> :
                        <div className={classes.addPhoto}>
                            {!props.isfollowed ?
                                <button onClick={() => props.follow()}>подписаться</button> :
                                <div className={classes.unfollow}>
                                    <button onClick={() => props.unfollow()}>отписаться</button>
                                </div>
                            }
                        </div>
                    }
                </div>
            </div>

            {editForm ? <WithReduxForm editFormInfo={editFormInfo} {...props} initialValues={profile} onSubmit={onSubmit} /> : <ProfileData {...props} editFormInfo={editFormInfo} isOuner={props.isOuner} />}

        </div>
    )
};

export default ProfileInfo;

