import { stopSubmit } from "redux-form";
import { AuthAPI, resultCodes, SecurityApi } from "../api/api";

const SET_USER_DATA = 'SET_USER_DATA';
const SET_CAPTCHA_URL = 'SET_CAPTCHA_URL';

// type initialType = {
//     id: null | number
//     email: null | string
//     login: null | string
//     isAuth: boolean | null
//     captcha: string | null
// }

// let initial: initialType = {
//     id: null, 
//     email: null,
//     login: null,
//     isAuth: false,
//     captcha: null
// }

let initial = {
    id: null as number | null,
    email: null as string | null,
    login: null as string | null,
    isAuth: null as boolean | null,
    captcha: null as string | null
}

export type initialType = typeof initial

let authReducer = (state = initial, action: actionTypes): initialType => {
    switch (action.type) {
        case SET_USER_DATA:

            return {
                ...state,
                ...action.data
            }
        case SET_CAPTCHA_URL:
            return {
                ...state,
                captcha: action.captchaUrl
            }
        default:
            return state;
    }
};

type actionTypes = setUserDataType | setCaptchaType

type dataType = {
    id: number | null
    email: string | null
    login: string | null
    isAuth: boolean | null
}
type setUserDataType = {
    type: typeof SET_USER_DATA
    data: dataType
}
export let setUserData = (id: number | null, email: string | null, login: string | null, isAuth: boolean | null): setUserDataType => { return { type: SET_USER_DATA, data: {id, email, login, isAuth } }};


type setCaptchaType = {
    type: typeof SET_CAPTCHA_URL
    captchaUrl: string
}
export let setCaptcha = (captchaUrl: string): setCaptchaType => { return {type: SET_CAPTCHA_URL, captchaUrl}}



export let Auth = () => async (dispatch: any) => {
       let data = await AuthAPI.Auth();
            if (data.resultCode === resultCodes.success) {
                let {id, email, login} = data.data;
                dispatch(setUserData(id, email, login, true))
            }
};

export let LogIn = (email: string, password: string, rememberMe: boolean, captcha: string | null) => async (dispatch: any) => {
        let response = await AuthAPI.Login(email, password, rememberMe, captcha = null);
            if (response.data.resultCode === resultCodes.success) {
                dispatch(Auth())
            } else {
                if(response.data.resultCode === resultCodes.captchaisrequired) {
                    dispatch(getCaptche())
                }
                let message = response.data.messages.length > 0 ? response.data.messages[0]: 'Some error';
                dispatch(stopSubmit('login', {_error: message}))
            }
};

export let LogOut = () => async (dispatch: any) => {
        let response = await AuthAPI.Logout();
            if (response.data.resultCode === resultCodes.success) {
                dispatch(setUserData(null, null, null, false))
            }
}

export let getCaptche = () => async (dispatch: any) => {
    let response = await SecurityApi.getCatpchaUrl();
    const captcha = response.data.url;
    dispatch(setCaptcha(captcha))
}

export default authReducer;