import React, { FC, useEffect } from 'react';
import classes from './Users.module.css';
import ava from '../../assets/avaImg/mal2.jpg';
import { NavLink } from 'react-router-dom';
import { usersType } from '../../types/types';

type PropsType = {
    user: usersType
    toggalIsFollowing: Array<number>
    UnFollowThunkCreater: (id: number) => void
    FollowThunkCreater: (id: number) => void
    getfollowed: (is: boolean) => void
}

const User: FC<PropsType> = (props) => {
    let u = props.user;

    return (
        <div className={classes.users}>
            <div>
                <NavLink to={`/Profile/` + u.id}><img onClick={() => props.getfollowed(u.followed)} src={!u.photos.large ? ava : u.photos.large} alt="" /></NavLink>
            </div>
            <div>
                <span className={classes.name}>
                    {u.name}
                </span>
            </div>
            <div>
                <div>
                    {
                        u.followed ? <button disabled={props.toggalIsFollowing.some((id: number) => id === u.id)} onClick={() => { props.UnFollowThunkCreater(u.id) }} className={classes.unfollowColor}>Отписаться</button>
                            : <button className={classes.followColor} disabled={props.toggalIsFollowing.some((id: number) => id === u.id)} onClick={() => { props.FollowThunkCreater(u.id) }}>Подписаться</button>
                    }
                </div>
            </div>
        </div>
    )
}

export default User;
