import React, { useState } from 'react'; import classes from './ProfileInfo.module.css';
import ProfileStatusWithHooks from './ProfileStatusWithHook';
import down from '../../../assets/avaImg/down2.png'
import MyLoader from '../../coummon/Prilouder/MyLouders/MtLouder';

const ProfileData = (props) => {

    let [editMode, SetEditMode] = useState(false);

    let profile = props.profile;

    const showContacts = () => {
        SetEditMode(!editMode)
    }

    let socialContacts = [];

    for (let contact in profile.contacts) {
        socialContacts.push(
            <div className={classes.natiolStrings}>
                <div className={classes.infoGrids}><span key={contact[socialContacts.length]} className={classes.item}>{contact}</span><span className={classes.serverStrings}>{profile.contacts[contact]}</span></div>
                <hr />
            </div>)
    };

    return (
        <div>
            {profile ?
                <div className={classes.userInfo}>
                    <div>
                        <span className={classes.userName}>{profile.fullName}</span>
                        {props.isOuner && <span onClick={props.editFormInfo} className={classes.editButton}>Редактировать</span>}
                        <ProfileStatusWithHooks status={props.status} upDataStatus={props.upDataStatus} isOuner={props.isOuner} /><hr />
                    </div>
                    <div className={classes.infoGridsBlock}>
                        <div className={classes.infoGrids}>
                            <span className={classes.item}>Обо мне</span><span className={classes.serverStrings}>{profile.aboutMe}</span>
                        </div>
                        <hr />
                        <div className={classes.infoGrids}>
                            <span className={classes.item}>Ищу работу </span><span className={classes.serverStrings}>{profile.lookingForAJob ? "да" : "нет"}</span>
                        </div>
                        <hr />
                        <div className={classes.infoGrids}>
                            <span className={classes.item}>Проф. навыки </span><span className={classes.serverStrings}>{profile.lookingForAJobDescription}</span>
                        </div>
                    </div>
                    <div>
                        <div onClick={showContacts} className={classes.contactsHead}><span>другие соцсети  </span> <img src={down} alt="arrow" /></div>
                        {
                            editMode ?
                                <div>
                                    <hr />
                                    <div className={classes.infoGridsBlock}>
                                        {socialContacts}
                                    </div>
                                </div> :
                                ''
                        }
                    </div>
                </div> :
                <MyLoader />}
        </div>
    )
};


export default ProfileData

