import React from 'react';
import { Suspense } from 'react';
import Prilouder from '../components/coummon/Prilouder/Prilouder';

function WithLaziSuspense(Component) {
            return (props) => {
                return <Suspense fallback={
                <div style={{height: "350px", padding: '150px 0 0 0', boxShadow: "0 0 1px 0 black"}}>
                    <h2 style={{opacity: 0.2}}>загрузка сообщений</h2>
                    <Prilouder/>
                </div>
                }>
                            <Component {...props}/>
                        </Suspense> 
            }
        }

export default WithLaziSuspense;
