import classes from './formControls.module.css';
import { Field, WrappedFieldsProps } from 'redux-form';
import React from 'react';
import { validtypes } from '../../../../Utils/validator/validator';
import { type } from 'os';


export const Textarea: React.FC<WrappedFieldsProps> = ({input, meta, ...props}) => {

    const HasError = meta.error;

    return (
        <div className={`${classes.formControl} ${HasError ? classes.error: ''}`}>
            <div>
                <textarea {...input} {...props} />
            </div>
            {HasError && <span>{meta.error}</span>}
        </div>
    )
};

export const TextareaPostsAndMessages: React.FC<WrappedFieldsProps> = ({input, meta, ...props}) => {

    return (
        <div className={`${classes.formControl} `}>
            <div>
                <textarea {...input} {...props} />
            </div>
        </div>
    )
};



export const Input: React.FC<WrappedFieldsProps> = ({input, meta, ...props}) => {

    const HasError = meta.touched && meta.error;

    return (
        <div className={`${classes.formControl} ${HasError ? classes.error: ''}`}>
            <div>
                <input {...input} {...props} />
            </div>
            {HasError && <span className={classes.error}>{meta.error}</span>} 
        </div>
    )
};

export type valuestypes = {
    email: string 
    password: string 
    rememderMe: boolean
    captcha: string | null
}

type loginvaluestype = keyof valuestypes



export const createField = (
    placeholder: string | undefined, 
    name: loginvaluestype, 
    component: React.FC<WrappedFieldsProps>,
    validate: Array<(value: string) => string | undefined>,
    props = {}, 
    text: string = '', 
    cols: string = '', 
    rows: string = '') => (
    <div>
        <Field placeholder={placeholder} name={name} component={component} validate={validate} {...props}cols={cols}rows={rows}/>
        {text}
    </div>)