import React from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router';
import { InjectedFormProps, reduxForm } from 'redux-form';
import { LogIn } from '../../redux/auth.reducer';
import { AppStateType } from '../../redux/redux-store';
import { maxLengthCreator, required } from '../../Utils/validator/validator';
import { createField, Input, valuestypes } from '../coummon/Prilouder/formControls/formControls';
import classes from './login.module.css'

const maxLength50 = maxLengthCreator(50);


type ountypes = {
    captcha: string | null
}

const LoginForm: React.FC<InjectedFormProps<valuestypes, ountypes> & ountypes> = ({ handleSubmit, error, captcha }) => {

    return (
        <form onSubmit={handleSubmit}>
            {createField("email", "email", Input, [required, maxLength50])}
            {createField("password", "password", Input, [required, maxLength50], { type: 'password' })}
            {createField(undefined, "rememderMe", Input, [], { type: 'checkbox' }, "запомнить")}
            <div className="error">
                {error && error}
            </div>
            <div>
                {captcha && <img src={captcha} alt="captcha" />}
                {captcha && createField("captcha", "captcha", Input, [required, maxLength50], {})}
            </div>
            <div>
                <button>Войти</button>
            </div>
        </form>
    )
};

const LoginReduxForm = reduxForm<valuestypes, ountypes>({ form: 'login' })(LoginForm)

type statetypes = {
    isAuth: boolean
    captcha: string
}

type dispatchtypes = {
    LogIn: (email: string, password: string, rememberMe: boolean, captcha: string | null) => void
}

const Login: React.FC<statetypes & dispatchtypes> = (props) => {

    const onSubmit = (formData: valuestypes) => {
        props.LogIn(formData.email, formData.password, formData.rememderMe, formData.captcha)
    }

    if (props.isAuth) {
        return <Redirect to={'/Profile'} />
    }

    return (
        <div className={classes.login}>
            <h1>Login</h1>
            <LoginReduxForm onSubmit={onSubmit} captcha={props.captcha} />
        </div>
    )
};

let mapStateToProps = (state: AppStateType): statetypes => ({
    isAuth: state.Auth.isAuth,
    captcha: state.Auth.captcha
})

export default connect(mapStateToProps, { LogIn: LogIn })(Login);

