import { Auth } from "./auth.reducer";

const SET_INITIAL = 'SET_INITIAL';

type initialType = {
    initialised: boolean
}

let initial: initialType = {
    initialised: false
}

let appReducer = (state = initial, action: ActionTypes): initialType => {
    switch (action.type) {
        case SET_INITIAL:
            return {
                ...state,
                initialised: true
            }
        default:
            return state;
    }
};

type ActionTypes = initialsuccsesType

type initialsuccsesType = {
    type: typeof SET_INITIAL
}

export let initialisedSuccess = (): initialsuccsesType => { return { type: SET_INITIAL }};

export let initialize = () => (dispatch: any) => {
     let promise =  dispatch(Auth());
     Promise.all([promise])
        .then(()=> {
            dispatch(initialisedSuccess())
     })
};

export default appReducer;