import { connect } from 'react-redux';
import React from 'react';
import { FollowThunkCreater, getfollowed, getUsersThunkCreater, UnFollowThunkCreater } from '../../redux/usersReducer';
import Users from './Users';
import Prilouder from '../coummon/Prilouder/Prilouder';
import { GetCarrentPage, GetIsFetching, GetPageSise, GetTotalCount, GetToggalIsFollowing, GetUsers } from '../../redux/users.selectors';
import { usersType } from '../../types/types';
import { compose } from 'redux';
import { AppStateType } from '../../redux/redux-store';

type MapStatePropsType = {
    carrentPage: number
    pageSise: number
    isFetching: boolean
    users: Array<usersType>
    toggalIsFollowing: Array<number>
    totalCount: number
}

type MapDispatchPropsType = {
    getUsersThunkCreater: (carrentPage: number, pageSise: number) => void
    FollowThunkCreater: (userId: number) => void
    UnFollowThunkCreater: (userId: number) => void
    getfollowed: (is: boolean) => void
}


type PropsType = MapStatePropsType & MapDispatchPropsType

class UserAPI extends React.Component<PropsType> {

    componentDidMount = () => {
        this.props.getUsersThunkCreater(this.props.carrentPage, this.props.pageSise)
    }
    setCurrentPage = (page: number) => {
        this.props.getUsersThunkCreater(page, this.props.pageSise)
    }
    render() {
        return (
            <>
                <Users 
                {...this.props} 
                setCurrentPage={this.setCurrentPage} 
                />
            </>
        )
    }
}


let MapStateToProps = (state: AppStateType): MapStatePropsType => {
    return {
        pageSise: GetPageSise(state),
        totalCount: GetTotalCount(state),
        users: GetUsers(state),
        carrentPage: GetCarrentPage(state),
        isFetching: GetIsFetching(state),
        toggalIsFollowing: GetToggalIsFollowing(state)
    }
};

export default compose(
    connect<MapStatePropsType, MapDispatchPropsType, AppStateType>(MapStateToProps, { getUsersThunkCreater, FollowThunkCreater, UnFollowThunkCreater, getfollowed })
    // @ts-ignore
)(UserAPI);