import { createSelector } from "reselect";
import { AppStateType } from "./redux-store";

const GetUsersSelector = (state: AppStateType) => {
    return state.UsersPage.users;
};

export const GetUsers = createSelector(GetUsersSelector, (users) => {
    return users.filter((user: any) => true)
})

export const GetFollowUsers = createSelector(GetUsersSelector, (users) => {
    return users.filter((user: any) => user.followed === true);
});


export const GetTotalCount = (state: AppStateType) => {
    return state.UsersPage.totalCount
};

export const GetPageSise = (state: AppStateType) => {
    return state.UsersPage.pageSise
};

export const GetCarrentPage = (state: AppStateType) => {
    return state.UsersPage.carrentPage
};

export const GetIsFetching = (state: AppStateType) => {
    return state.UsersPage.isFetching
};

export const GetToggalIsFollowing = (state: AppStateType) => {
    return state.UsersPage.toggalIsFollowing
}