import React from 'react';
import { usersType } from '../../types/types';
import Paginator from '../coummon/padinator';
import MyLoader from '../coummon/Prilouder/MyLouders/MtLouder';
import Prilouder from '../coummon/Prilouder/Prilouder';
import User from './User';
import classes from './Users.module.css';


type Props = {
    isFetching: boolean
    users: Array<usersType>
    totalCount: number
    pageSise: number
    carrentPage: number
    setCurrentPage: (p: number) => void
    toggalIsFollowing: Array<number>
    UnFollowThunkCreater: (userId: number) => void
    FollowThunkCreater: (userId: number) => void
    getfollowed: (is: boolean) => void
}


let Users: React.FC<Props> = (props) => {

    return (

         <div className={classes.usersBlock}>
            <Paginator totalCount={props.totalCount} pageSise={props.pageSise} carrentPage={props.carrentPage} setCurrentPage={props.setCurrentPage} />
            <div>
                {!props.isFetching ?
                props.users.map((u: any, index: number) => 
                 <User
                    key={index}
                    user={u}
                    toggalIsFollowing={props.toggalIsFollowing}
                    UnFollowThunkCreater={props.UnFollowThunkCreater}
                    FollowThunkCreater={props.FollowThunkCreater}
                    getfollowed={props.getfollowed}
                />
            ): 
            <div style={{height: "360px"}}>
                <div style={{marginTop: '150px'}}>
                    <h2 style={{opacity: 0.2}}>загрузка пользователей</h2>
                <Prilouder/>
                </div>
            </div>
            }
            </div>
        </div>
    )
};

export default Users;