import React, { FC } from 'react';
import { profileType } from '../../types/types';
import Prilouder from '../coummon/Prilouder/Prilouder';
import classes from './Profile.module.css';
import ProfileInfo from './ProfileInfo/ProfileInfo';


type propsType = {
    profile: profileType
    status: string
    upDataStatus: () => void
    toggalIsFollowing: Array<number>
    isfollowed: boolean
    isOuner: boolean
    saveFile: () => void
    saveInfo: () => void
    unfollow: () => void
    follow: () => void
}

const Profile: FC<propsType> = (props) => {

    return (
        <div className={classes.profile}>
           {!props.profile ?
                <div style={{height: "350px", borderRadius: "10px", padding: '50px 0 0 0', boxShadow: "0 0 2.5px -1px black"}}>
                    <h2 style={{opacity: 0.2}}>загрузка профиля</h2>
                    <Prilouder />
                </div> : <ProfileInfo 
                profile={props.profile}
                isfollowed={props.isfollowed}
                status={props.status}
                upDataStatus={props.upDataStatus}
                toggalIsFollowing={props.toggalIsFollowing}
                isOuner={props.isOuner}
                saveFile={props.saveFile}
                saveInfo={props.saveInfo}
                follow={props.follow} 
                unfollow={props.unfollow}
            />}
        </div>
    )
};


export default Profile;