import React from 'react';
import { NavLink } from 'react-router-dom';
import classes from './Header.module.css';


const Header = (props) => {
    return (
        <header className={classes.header}>
            <div className={classes.h1}>
                <span className={classes.suan}>suanchik</span>
            </div>
            {props.isAuth ? <div className={classes.logBlock}><span className={`${classes.out} ${classes.span}`} onClick={props.LogOut}>Выйти</span></div> : <NavLink to={`/Login`}><div className={classes.logBlock}>Войти</div></NavLink>}
        </header>
    )
};


export default Header;