let initial = {
    posts: [
        {message: `Да, что бы ни говорили некоторые политики, изменение климата – это факт. 
                    За последние 140 лет среднегодовая температура повысилась примерно на 1°С.
                    Важно понимать, что за время своего существования климат Земли менялся довольно часто. 
                    Это связано, прежде всего, с изменениями орбиты нашей планеты: орбита то «вытягивается», 
                    то становится более округлой (цикл составляет примерно 93 тысячи лет).`, 
        likes: '100', id: '1'}
    ] as Array<postsType>,
    profile: null as profileType | null,
    status: '',
}

export type initialType = typeof initial

let profileReducer = (state = initial, action: actionTypes): initialType => {
    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                posts: [...state.posts, {message: action.postBody, likes: '0', id: action.id}]
            }
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(p => p.id != action.postId)
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case SET_STATUS: 
            return {
                ...state,
                status: action.status
            }
        case SET_PHOTO: 
            return {
                ...state,
                profile: {...state.profile, photos: action.photo} as profileType
            }
        default:
            return state;
    }
};

type actionTypes = setPhototype | setUserProfiletype | addPostACtype | deletePostACtype | setStatustype

type setPhototype = {
    type: typeof SET_PHOTO
    photo: photoType
}
export let setPhoto = (photo: photoType): setPhototype => { return { type: SET_PHOTO, photo}}

type setUserProfiletype = {
    type: typeof SET_USER_PROFILE
    profile: profileType
}
export let setUserProfile = (profile: profileType): setUserProfiletype => { return { type: SET_USER_PROFILE, profile } }

type addPostACtype = {
    type: typeof ADD_POST
    postBody: string
    id: string
}
export let addPostAC = (postBody: string, id: string): addPostACtype => { return { type: ADD_POST, postBody, id} }

type deletePostACtype = {
    type: typeof DELETE_POST
    postId: string
}
export let deletePostAC = (postId: string): deletePostACtype => { return { type: DELETE_POST, postId} }

type setStatustype = {
    type: typeof SET_STATUS
    status: string
}
export let setStatus = (status: string): setStatustype => { return {type: SET_STATUS, status} }





export let getProfile = (userId: number) => async (dispatch: any) => {
        let response = await ProfileAPI.getProfile(userId);
            dispatch(setUserProfile(response.data))
}

export let getUserStatus = (userId: number) => async (dispatch: any) => {
       let response = await ProfileAPI.getStatus(userId);
            dispatch(setStatus(response.data))
}

export let upDataStatus = (status: string) => async (dispatch: any) => {
        try{
            let response = await ProfileAPI.updateStatus(status);
            if(response.data.resultCode === 0) {
                dispatch(setStatus(status))
            }
        }catch(error){
            alert(error)
        }
};

export let saveFile = (photo: photoType) => async (dispatch: any) => {
    let response = await ProfileAPI.savePhoto(photo);
        if(response.data.resultCode === 0) {
            dispatch(setPhoto(response.data.data.photos))
        }
}

export let saveInfo = (profile: profileType) => async (dispatch: any, getState: any) => {
    const userId = getState().Auth.id
    let response = await ProfileAPI.saveProfile(profile);
    debugger;
        if(response.data.resultCode === 0) {
            dispatch(getProfile(userId))
        } else {
            let message = response.data.messages.length > 0 ? response.data.messages[0]: 'Some error';
            dispatch(stopSubmit('edit-profile', {_error: message}))
        }
}




export default profileReducer;