const ADD_MESSAGE = 'ADD_MESSAGE';
const DELETE_MESSAGE = 'DELETE_MESSAGE'

type dialogType = {
  id: string | null
  message: string | null
  author: string | null
}

type messageType = {
  id: string | number 
  dialogname: string 
  img?: any
  dialog: Array<dialogType>
}

type messagesListType = {
  messagesList: Array<messageType>
}

