let messagesReducer = (state = initial, action: any): messagesListType => {
    switch (action.type) {
        case ADD_MESSAGE:{
          const findedIndex = state.messagesList.findIndex(list => list.id === action.dialogId);
          const newList = state.messagesList.map(list => { return {...list}});
          newList[findedIndex].dialog.push(action.messageBody);
          return {
            ...state,
            messagesList: newList
          }
        }
        case DELETE_MESSAGE:{
          const newDialog = state.messagesList[action.elementIndex].dialog.filter(el => el.id !== action.id);
          const newList = state.messagesList.map(list => { return {...list}});
          newList[action.elementIndex].dialog = newDialog;
          return {
            ...state,
            messagesList: newList
          }
        }
        default:
            return state;
    }
};

type actionTypes = addMessageACType | deleteMessageType

type addMessageACType = {
  type: typeof ADD_MESSAGE
  messageBody: string
  dialogId: number | string
}

export let addMessageAC = (messageBody: string, dialogId: string): addMessageACType => {
  return {type: ADD_MESSAGE, messageBody, dialogId}
}

type deleteMessageType = {
  type: typeof DELETE_MESSAGE
  elementIndex: number
  id: number | string
}

export let deleteMessage = (elementIndex: number, id: number | string): deleteMessageType => ({type: DELETE_MESSAGE, elementIndex, id})
 
export default messagesReducer;

